.panel {
    display: flex;
    justify-content: start;
    align-items: center;
    width: 100%;
    height: 270px;
    background: rgba(64, 64, 64, 0.6);
    border-radius: 0px 0px 0px 22px;
    font: 34px 'Open Sans', sans-serif;
    color: #FFFFFF;
    padding-left: 30px;
    box-sizing: border-box;
}
