@import '../../index.css';

.wrapper {
    height: calc(100vh - 117px);
    margin-top: 5px;
    overflow-y: scroll;
    padding: 55px 40px 0 0;
    mask-image: linear-gradient(transparent, black 45px), linear-gradient(to left, black 10px, transparent 10px);
}

.titleWrapper, .taskWrapper, .subTaskWrapper {
    box-sizing: border-box;
    font: 34px 'Open Sans', sans-serif;
}

.title, .task {
    box-sizing: border-box;
    background: var(--main-panel-bg);
    font: 34px 'Open Sans', sans-serif;
    color: #FFFFFF;
    resize: none;
    overflow: hidden;
    text-align: justify;
    -moz-text-align-last: center;
    text-align-last: center;
    border: none;
    box-shadow: -2px 3px 6px 3px rgba(0, 0, 0, 0.15);
}

.title:not(:disabled) {
    background: 10px 10px / 25px no-repeat url('data:image/svg+xml;base64,'),
                var(--title-panel-bg);
}

.task:not(:disabled) {
    background: 10px 10px / 25px no-repeat url('data:image/svg+xml;base64,'),
                var(--main-panel-bg);
}

.title {
    background: var(--title-panel-bg);
    padding: 35px 35px 35px 50px;
    border-radius: 0 22px 22px 0;
    margin-bottom: 75px;
    width: 100%;
}

.taskWrapper {
    position: relative;
    margin-bottom: 35px;
    width: 100%;
    animation: .15s 0s ease-in-out taskAppearanceAnimation;
}

.addBtnTransform {
    transition: .15s;
    transform: translateY(calc(100% + 35px));
}

@keyframes taskAppearanceAnimation {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(0); }
}

.task {
    display: inline-block;
    padding: 25px 25px 25px 50px;
    width: calc(100% - 107px);
}

.addTaskBtn {
    display: block;
    width: 100%;
    border: none;
    cursor: pointer;
    border-radius: 0 22px 22px 0;
    background: center / 50px no-repeat url('data:image/svg+xml;base64,'), 
                var(--main-panel-bg) !important;
}

.taskInnerWrapper {
    display: flex;
    align-items: stretch
}

.taskBtn {
    margin-left: 12px;
    display: inline-block;
    width: 95px;
    border-radius: 0 22px 22px 0;
    border: none;
    cursor: pointer;
    box-shadow: -2px 3px 6px 3px rgba(0, 0, 0, 0.15);
}

.deleteBtn {
    background-color: rgba(255, 126, 126, .8) !important;
    background: center / 50px no-repeat url('data:image/svg+xml;base64,');
}

.completed {
    background-color: rgba(126, 255, 170, .8);
}

.uncompleted {
    background-color: rgba(255, 126, 126, .8);
}

.deletion {
    animation: .15s 0s ease-in forwards deletingAnimationFirst,
               .15s .15s ease-out forwards deletingAnimationSecond;
}

@keyframes deletingAnimationFirst {
    100% {
        transform: translateX(-100%);
    }
}

@keyframes deletingAnimationSecond {
    100% {
        max-height: 0px;
        margin-bottom: 0px;
    }
}
